$wizard-highligh-color: darken($body-bg, 5%);

.rxn-wizard {
  border: 1px solid $wizard-highligh-color;

  .title,
  .content,
  .nav {
    padding: $spacing-unit;
  }

  .title {
    background: $wizard-highligh-color;

    button {
      margin-left: $spacing-unit/2;
    }
  }

  h2 {
    margin: 0;
    @include text-size(lg);
  }

  .content {
    & > *:first-child {
     margin-top: 0;
    }
  }

  .nav {
    border-top: 1px solid $wizard-highligh-color;
    padding-top: $spacing-unit/2;
    padding-bottom: $spacing-unit/2;
  }
}
